import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatTableDataSource } from '@angular/material/table';
import {
  FileProcessingSummaryDTO,
  ExcelDataProcessingMultiDateReqDTO
} from 'src/app/shared/models/file-processing-summary-dto';
import { MedMasterDto } from 'src/app/shared/models/medmasterdto';
import { ExcelDataProcessingRespDTO } from 'src/app/shared/models/excel-data-processing-resp-dto';
import { ApiService } from 'src/app/shared/services/api.service';
import { ModalDataModificationsDetailComponent } from '../modals/modal-data-modifications-detail/modal-data-modifications-detail.component';
import Swal from 'sweetalert2';
import { User } from 'src/app/shared/models/user';
import { UsersService } from '../users/users.service';
import { ExcelService } from 'src/app/shared/services/excel.service';
import { DatePipe } from '@angular/common';

@Component({
  selector: 'app-excel-data-process-summary',
  templateUrl: './excel-data-process-summary.component.html',
  styleUrls: ['./excel-data-process-summary.component.scss']
})
export class ExcelDataProcessSummaryComponent implements OnInit {
  public fileProcessingSummaryList: Array<FileProcessingSummaryDTO>;

  extSysNameData: string;
  processNameData: string;
  selectorDateDataFrom: Date;
  selectorDateDataTo: Date;
  selected = 'option2';
  data: any;
  user_id:any;
  screenName:any;
  role:any;
  res:any;
  rolepermission:any;
  button_permission:any={};
  listMedMaster = new Array<MedMasterDto>();
  processDataDto: ExcelDataProcessingRespDTO;
  excelDataProcessingMultiDateReqDTO: ExcelDataProcessingMultiDateReqDTO = new ExcelDataProcessingMultiDateReqDTO();
  extdataProcessSummaryAuditLogDetails = new User();
  fileProcessingSummaryDTO: Array<FileProcessingSummaryDTO>;

  allExternalSystemNames: string[] = [];
  processNames: string[] = [
    'DEUpload',
    'CustomerQuery',
    'CreateCustomer',
    'QueryContract'
  ];

  displayedColumns: string[] = [
    'externalSystem',
    'processName',
    'date',
    'totalRecords',
    'recordsProcessed',
    'recordsFailed',
    'amountMismatch',
    'processIteration',
    'process'
  ];
  pipe = new DatePipe('en-US');
  dataSource = new MatTableDataSource<FileProcessingSummaryDTO>(
    this.fileProcessingSummaryDTO
  );
  dataforsummary:any;
  constructor(
    private api: ApiService,
    public dialog: MatDialog,
    private userApi: UsersService,
    private snackBar: MatSnackBar,
    private excelService: ExcelService
  ) { }

  ngOnInit() {
    this.user_id=sessionStorage.getItem('user_id');
    this.role=sessionStorage.getItem('user_role');

    this.getAllExternalSystemNames();
    this.extSysNameData = '';
    this.processNameData = '';
    this.screenpermission();
  }
  screenpermission(){
    this.screenName='roles';
    //this.role='ROLE1';
    this.userApi.getRoleScreenPermission(this.user_id,this.screenName,this.role).subscribe(res=>{
      this.rolepermission=res;
      //console.log(this.rolepermission);
 if(this.rolepermission){
        this.button_permission=this.rolepermission[0];
        //console.log( this.button_permission);
        }
    });
   
  }

  getSummaryData(){
    this.api.getDataForSummary().subscribe(dataforsummary=>{
      this.dataforsummary=dataforsummary;
      console.log(this.dataforsummary);
    })
  }
  doFirstValidation() {
    //console.log('External Sys Name : ' + this.extSysNameData);
    //console.log('process Name: ' + this.processNameData);
    //console.log('date selected from: ' + this.selectorDateDataFrom);
    //console.log('date selected to: ' + this.selectorDateDataTo);

    if (
      this.extSysNameData === '' ||
      this.processNameData === '' ||
      this.selectorDateDataFrom === undefined ||
      this.selectorDateDataTo === undefined
    ) {
      Swal.fire({
        type: 'error',
       
        text: 'Please fill all the details ',
      });
    } else {
      this.excelDataProcessingMultiDateReqDTO.extSysName = this.extSysNameData;
      this.excelDataProcessingMultiDateReqDTO.processName = this.processNameData;
      this.excelDataProcessingMultiDateReqDTO.processingDateFrom = this.selectorDateDataFrom;
      this.excelDataProcessingMultiDateReqDTO.processingDateTo = this.selectorDateDataTo;

      //console.log('excelDataProcessingMultiDateReqDTO :');
      //console.log(this.excelDataProcessingMultiDateReqDTO);

      this.api
        .getExcelDataProcessSummaryListService(
          this.excelDataProcessingMultiDateReqDTO
        )
        .subscribe(data => {
          this.data = data;
          if (this.data.length === 0) {
            Swal.fire({
              type: 'error',
             
              text: 'Data not found for above credentials',
            });
            return;
          }
          this.fileProcessingSummaryDTO = this.data;

          //console.log('fileProcessingSummaryDTO');
          //console.log(this.fileProcessingSummaryDTO);
          this.dataSource = new MatTableDataSource<FileProcessingSummaryDTO>(
            this.fileProcessingSummaryDTO
          );
        },
          error => {
            Swal.fire({
              type: 'error',
            
              text: 'server error',
              footer: '<a >Please contact admin Team</a>'
            })
          });
    }
  }
  exitFileProcessing() {
    this.fileProcessingSummaryDTO = null;
    this.extSysNameData = '';
    this.processNameData = '';
    this.selectorDateDataFrom = null;
    this.selectorDateDataTo = null;
  }

  openDialog(fileProcessingSummaryDTO: FileProcessingSummaryDTO) {
    const dialogRef = this.dialog.open(ModalDataModificationsDetailComponent, {
      data: fileProcessingSummaryDTO
    });
    dialogRef.afterClosed().subscribe(result => {
    });
  }

  openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 2000
    });
  }

  getAllExternalSystemNames() {
    this.api.getAllExtSysNamesServiceAll().subscribe(resp => {
      //console.log(resp);
      this.allExternalSystemNames = resp;
    });
  }

  getProcessNamesBasedOnExtSysName(extSysName: string) {
    //console.log('inside get ProcessNames');
    this.api
      .getProcessNamesBasedOnExtSysNameService(extSysName)
      .subscribe(resp => {
        //console.log(resp);
        this.processNames = resp;
      });
  }
 
  getData(){
    this.fileProcessingSummaryDTO.forEach(element => {
     element.date=this.pipe.transform(element.date, 'dd-MMM-yy');
    });
    Swal.fire('Downloading report will take take time based on the data','click ok and wait.')
    this.excelService.exportAsExcelFile( this.fileProcessingSummaryDTO, 'File_Upload_Report');
  }
}
