import { Component, OnInit, ViewChild, Inject } from "@angular/core";
import { MatPaginator } from "@angular/material/paginator";
import { MatTableDataSource } from "@angular/material/table";
import { ModalExcelAuthFirstComponent } from "../modal-excel-auth-first/modal-excel-auth-first.component";
import { MatDialog, MAT_DIALOG_DATA } from "@angular/material/dialog";
import { MatSnackBar } from "@angular/material/snack-bar";
// tslint:disable-next-line: max-line-length
import { ApiService } from 'src/app/shared/services/api.service';
import { PendingForAuthDetailsDTO } from 'src/app/shared/models/pending-for-auth-datails-dto';
import { FileProcessingSummaryDTO } from 'src/app/shared/models/file-processing-summary-dto';
import { ExcelService } from 'src/app/shared/services/excel.service';
import Swal from 'sweetalert2';
import { DatePipe } from '@angular/common';

@Component({
  selector: "app-modal-data-modifications-detail",
  templateUrl: "./modal-data-modifications-detail.component.html",
  styleUrls: ["./modal-data-modifications-detail.component.scss"]
})
export class ModalDataModificationsDetailComponent implements OnInit {
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;

  pendingForAuthDataDetails: any;
  flag: boolean;
  fileProcessingSummaryDTO: FileProcessingSummaryDTO;
  public dummyPendingForAuthDataDetails: Array<PendingForAuthDetailsDTO>;

  dataSource = new MatTableDataSource<PendingForAuthDetailsDTO>(
    this.pendingForAuthDataDetails
  );
  pipe = new DatePipe('en-US');
  displayedColumns: string[] = [
    "uploadStat",
    "account",
    "ccyCd",
    "branchCode",
    "amount",
    "lcyEquivalent",
    "drCr",
    "exchRate",
    "relatedAccount",
    "relCust",
    "addlText",
    "view"
  ];

  ngOnInit() {
    this.flag = true;
    this.getAllDetailsData();
    this.dataSource.paginator = this.paginator;
    //console.log(this.pendingForAuthDataDetails);
  }
  constructor(
    @Inject(MAT_DIALOG_DATA) private data,
    private api: ApiService,
    public dialog: MatDialog,
    private snackBar: MatSnackBar,
    private excelService: ExcelService
  ) {}

  openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 2000
    });
  }

  approveAndProcess() {
    this.openSnackBar("Data Sent for approval", "SUCCESS");
    this.flag = false;
  }

  openDialog(detailsDto: PendingForAuthDetailsDTO) {
    const dialogRef = this.dialog.open(ModalExcelAuthFirstComponent, {
      data: detailsDto
    });

    dialogRef.afterClosed().subscribe(result => {
      //console.log(`Dialog result: ${result}`);
    });
  }
  getAllDetailsData() {
    this.fileProcessingSummaryDTO = this.data;
    //console.log("fileProcessingSummaryDTO in modal:");
    //console.log(this.fileProcessingSummaryDTO);
    this.api
      .getDetailsListBasedOnFileSummaryMaster(this.fileProcessingSummaryDTO)
      .subscribe(resp => {
        this.pendingForAuthDataDetails = resp;
        if (this.pendingForAuthDataDetails.length === 0) {
          this.flag = false;
          this.openSnackBar("Data Not Available", "");
          return;
        }
        //console.log(this.pendingForAuthDataDetails);

        this.dataSource = new MatTableDataSource<PendingForAuthDetailsDTO>(
          this.pendingForAuthDataDetails
        );
        this.dataSource.paginator = this.paginator;
      });
  }
  dataForProcessScreen1:any[]=[];
  getData(){
    console.log(this.pendingForAuthDataDetails);
    this.pendingForAuthDataDetails.forEach(element => {
      
      this.dataForProcessScreen1.push({
        'Source_Code':element.id.sourceCode,
        'Process_Code': element.processCode,
        'Branch_Code': element.id.branchCode,
        'Batch_No': element.id.batchNo,
    
        'Value_Date': this.pipe.transform(element.valueDate, 'dd-MMM-yy'),
        
        Account: element.account,
        Account_Branch: element.accountBranch,
        Currency: element.ccyCd,
        Amount: element.amount,
        DrCr: element.drCr,
        LcyEquivalent: element.lcyEquivalent,
        Exch_Rate: element.id.exchRate,
        Curr_No: element.id.currNo,
        Addl_Text: element.addlText,
        Trn_Code: element.txnCode,
        Period_Code: element.periodCode,
        Financial_Cycle: element.finCycle,
        Initiation_Date: this.pipe.transform(element.initiationDate, 'dd-MMM-yy'),
        Upload_Date: this.pipe.transform(element.uploadDate, 'dd-MMM-yy'),
        Input_By: element.inputBy,
        Input_Time: this.pipe.transform(element.inputTime, 'dd-MMM-yy'),
        UDF_Detail: element.udfDetails,
        Error_Desc:element.errorDesc,
        Validation_Error: element.validationError,
        Related_Customer: element.relCust,
        Related_Account: element.relatedAccount,
        Related_Reference: element.relatedRef
      });
      console.log(this.dataForProcessScreen1);
    
    });
    console.log('final data',this.dataForProcessScreen1);
    Swal.fire('Downloading report will take take time based on the data','click ok and wait.')
    this.excelService.exportAsExcelFile( this.dataForProcessScreen1, 'File_Summary_Report');
  }
}
